.form {
  padding-top: 191px;
  padding-bottom: 209px;
}

.form__wrap {
  display: flex;
}

.form-left {
  max-width: 367px;
  width: 100%;
}

.form__title {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 87.5%;
  letter-spacing: -0.02em;
  color: #5e534a;
}

.header-contacts.form-contacts {
  margin-top: 48px;
  margin-left: 0;

  .header-menu__email {
    margin-top: 20px;
  }
}

.form-socials {
  margin-top: 80px;
  display: flex;

  a {
    margin-right: 32px;
    width: 56px;
    height: 56px;

    svg {
      width: 21px;
      height: 21px;
    }
  }
}

.form-right {
  max-width: 878px;
  width: 100%;
  margin-left: 147px;
}

.form-right__title {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 37px;
  line-height: 45px;
  color: #5e534a;
}

.form-row {
  display: flex;
  align-items: center;
  margin-top: 36px;
  justify-content: space-between;
  align-items: baseline;

  input[type='text'] {
    width: 362px;
  }

  input[type='tel'] {
    width: 213px;
  }

  input[type='email'] {
    width: 213px;
  }

  &:nth-child(2) {
    margin-top: 89px;
  }
}

.choices {
  width: 362px;
}

.choices__inner {
  background: none;
  border: none;
  border-bottom: 1px solid #d7c9c3;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
  color: #787878;
  display: block;
  padding: 14px 16px !important;
}

.choices__list--single {
  padding: 0;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted:after {
  display: none;
}

.form-textarea {
  width: 473px;
}

.form-btn {
  padding: 17px 131px 16px 23px;
  position: relative;
  margin-top: 26px;

  &:before {
    content: '';
    position: absolute;
    background-image: url('../img/form-btn-arr.svg');
    width: 16px;
    height: 8px;
    background-repeat: no-repeat;
    top: 50%;
    right: 27px;
    transform: translateY(-50%);
  }
}

.form-checkbox {
  display: block;
  margin-top: 30px;
}

.js-choice {
  position: relative;
  display: inline-block;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.choices[data-type*='select-one']:after {
  content: '';
  position: absolute;
  right: 13px;
  top: 18px;
  border: none;
  background-image: url('../img/chevron.svg');
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  transition: all 0.3s ease;
}

.choices.is-open:after {
  transform: rotate(180deg);
  margin-top: 0 !important;
}

@media screen and (max-width: 1440px) {
  .form-right {
    margin-left: auto;
  }
}

@media screen and (max-width: 1200px) {
  .form {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  .form__wrap {
    flex-direction: column;
  }

  .form-socials {
    margin-top: 30px;
  }

  .form__title {
    text-align: center;
    font-size: 60px;
  }

  .form-left {
    max-width: none;
  }

  .form-right {
    margin-top: 50px;
    max-width: none;

    input {
      width: 30% !important;
      font-size: 16px;
      padding: 10px;
    }

    select {
      padding: 10px;
    }

    .choices {
      width: 49%;
    }

    textarea {
      width: 49%;
      font-size: 16px;
      padding: 10px;
    }
  }

  .form-right__title {
    text-align: center;
  }

  .form-row:nth-child(2) {
    margin-top: 60px;
  }

  .form-btn {
    margin-top: 30px;
  }
}

@media screen and (max-width: 1024px) {
  .form {
    padding-top: 40px;
    padding-bottom: 30px;
  }
  .form__title {
    font-size: 48px;
  }

  .form-right__title {
    font-size: 32px;
  }

  .form-socials {
    margin-top: 20px;
  }

  .form-row {
    margin-top: 30px;
  }

  .form-row:nth-child(2) {
    margin-top: 40px;
  }
  .form-btn {
    margin-top: 10px;
  }
}

@media screen and (max-width: 768px) {
  .form__title {
    text-align: left;
    font-size: 32px;
  }

  .form {
    padding-bottom: 60px;
    .header-contacts .header-menu__phone {
      font-size: 20px;
    }

    .header-contacts.form-contacts {
      margin-top: 20px;
      line-height: 24px;
    }

    .header-contacts.form-contacts .header-menu__email {
      margin-top: 12px;
    }

    .form-socials {
      max-width: 199px;
      width: 100%;
      margin-top: 36px;

      a {
        width: 46px;
        height: 46px;

        svg {
          width: 20px;
          height: 20px;
        }
      }
    }
  }

  .form-right {
    margin-top: 40px;
  }

  .form-row {
    flex-direction: column;

    input {
      width: 100% !important;
      padding: 13px 12px;
      margin-bottom: 32px;
    }

    textarea {
      width: 100%;
      padding: 13px 12px;
      margin-bottom: 0;
    }

    .choices {
      width: 100%;
      margin-bottom: 32px;

      .choices__inner {
        padding: 13px 12px;
      }
    }
  }

  .form-row:nth-child(2) {
    margin-top: 0;
  }

  .form-right__title {
    font-size: 22px;
    line-height: 27px;
  }

  .form-btn {
    margin-top: 30px;
  }

  .form-checkbox {
    .checkbox__text {
      font-size: 12px;
      line-height: 14px;
      padding-left: 50px;
      max-width: 250px;
    }

    .checkbox__text:before {
      top: 1px;
      width: 38px;
      height: 38px;
    }

    input:checked + .checkbox__text:after {
      width: 22px;
      height: 22px;
      top: 9px;
      left: 8px;
    }
  }
}

@media screen and (max-width: 425px) {
  .form {
    padding-top: 68px;
    padding-bottom: 50px;
  }

  .form__title {
    max-width: 240px;
    line-height: 112.5%;
  }
  .form .header-contacts.form-contacts {
    margin-top: 24px;
  }

  .form-right {
    margin-top: 43px;
  }

  .form-row input {
    padding: 10px 12px;
    margin-bottom: 27px;
  }

  .choices__inner {
    padding: 10px 12px;
  }

  .form-row .choices {
    width: 100%;
    margin-bottom: 82px;
  }
}
