.footer {
  background: #FAF1E7;
  padding-top: 55px;
  padding-bottom: 55px;
}

.footer__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-logo__name {
  font-size: 33px;
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: bold;
  line-height: 119%;
  letter-spacing: 0.33em;
  text-transform: uppercase;
  color: #5D534A;
}

.footer-logo__subtitle {
  font-family: 'MuseoSans', sans-serif;
  margin-top: 1px;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.025em;
  text-transform: uppercase;
  color: #cccccc;
  position: relative;
  width: 100%;
  text-align: center;
  position: relative;
  &:before,
  &:after {
    content: '';
    position: absolute;
    width: 90px;
    height: 1px;
    background-color: #ccc;
    top: 50%;
    transform: translateY(-50%);
  }

  &:before {
    left: 0;
  }

  &:after {
    right: 0;
  }
}

.footer-socials {
  display: flex;
  width: 220px;
  justify-content: space-between;
  margin-top: 35px;
  a {
    width: 56px;
    height: 56px;
    svg {
      width: 20px;
      height: 20px;
    }
  }
}

.footer__nav {
  margin-top: 66px;
}

.footer-ul {
  display: flex;
}

.footer-li {
  margin-right: 60px;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #99928A;

  &:last-child {
    margin-right: 0;
  }
}


.footer-divider {
  width: 100%;
  height: 1px;
  background-color: #e7e7e7;
  margin-top: 19px;
}

.footer-copyright {
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 150%;
  color: #A2A3A4;
  text-align: center;
  margin-top: 22px;
}

@media screen and (max-width: 1024px) {
  .footer {
    padding-top: 40px;
    padding-bottom: 20px;
  }
  .footer-logo__name {
    font-size: 28px;
  }

  .footer-logo__subtitle {
    font-size: 14px;
  }

  .footer-socials {
    margin-top: 20px;
  }

  .footer__nav {
    margin-top: 40px;
  }
}


@media screen and (max-width: 768px) {
  .footer {
    padding-bottom: 18px;
  }

  .footer-logo__name {
    font-size: 18px;
  }

  .footer-logo__subtitle {
    font-size: 10px;
  }

  .footer-logo__subtitle:before, .footer-logo__subtitle:after {
    width: 52px;
  }
  .footer-li {
    margin-right: 0;
    width: 50%;
    font-size: 14px;
    margin-bottom: 13px;
  }

  .footer-ul {
    flex-wrap: wrap;
  }

  .footer-copyright {
    margin-top: 14px;
  }
}