@import '../../node_modules/normalize.css/normalize';
@import '../../node_modules/swiper/swiper-bundle';

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

.container {
  max-width: 1550px;
  padding: 0 15px;
  margin: 0 auto;
}

.style-info__text + [data-readmore-toggle],
.style-info__text[data-readmore] {
  display: inline-block !important;
  width: auto !important;
}

button {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}

.slider-arr-next,
.slider-arr-prev {
  transition: all 0.3s ease;
  &:hover {
    background: #9bc8b8;
  }
  &:hover {
    background: #7db4a1;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  padding: 0;
  margin: 0;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}

img {
  width: 100%;
}

body {
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: 100;
}

input {
  background: none;
  border: none;
  border-bottom: 1px solid #d7c9c3;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
  color: #787878;
  display: block;
  padding: 14px 16px;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
}

select::-ms-expand {
  display: none;
}

textarea {
  background: none;
  border: none;
  border-bottom: 1px solid #d7c9c3;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
  color: #787878;
  padding: 14px 16px;
}

.checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.checkbox__text {
  position: relative;
  padding: 0 0 0 40px;
  cursor: pointer;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  max-width: 450px;
  width: 100%;
  color: #a39687;
}

.checkbox__text:before {
  content: '';
  position: absolute;
  top: 3px;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #d7c9c3;
  transition: 0.2s;
}

.checkbox input:checked + .checkbox__text:after {
  content: '';
  position: absolute;
  top: 8px;
  left: 5px;
  width: 18px;
  height: 18px;
  transition: 0.2s;
  background-size: cover;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='D7C9C3' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

.btn {
  font-family: 'MuseoSans', sans-serif;
  padding: 22px 68px 20px 67px;
  background: #faf1e7;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 187.5%;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #5e534a;
  transition: all 0.3s ease;
  &:hover {
    background: #9bc8b8;
    color: #fff;
  }
  &:active {
    background: #7db4a1;
    color: #fff;
  }
}

.swiper-button-next,
.swiper-button-prev {
  &:after {
    display: none;
  }
}

.slider-arr-next,
.slider-arr-prev {
  position: relative;
  overflow: visible;
  &:before {
    content: '';
    position: absolute;
    background-repeat: no-repeat;
    background-size: cover;
    width: 56px;
    top: 50%;
    transform: translateY(-50%);
    height: 5px;
  }
}

.services-item__btn {
  transition: all 0.3s ease;
  &:hover {
    background: #9bc8b8;
    color: #fff;
  }
  &:active {
    background: #7db4a1;
    color: #fff;
  }
}

.slider-arr-next:before {
  background-image: url('../img/slider-arr-next.svg');
  right: 50px;
}

.slider-arr-prev:before {
  background-image: url('../img/slider-arr-prev.svg');
  left: 50px;
}

.slider-arr-next,
.slider-arr-prev {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  background: #faf1e7;
  &-white {
    background: #fff;
  }
}

@media screen and (max-width: 1024px) {
  .btn {
    font-size: 12px;
    padding: 12px 38px 11px 37px;
  }

  .checkbox__text {
    font-size: 14px;
  }

  .checkbox__text:before {
    width: 20px;
    height: 20px;
  }

  .checkbox input:checked + .checkbox__text:after {
    width: 12px;
    height: 12px;
    top: 7px;
    left: 4px;
  }
}

@media screen and (max-width: 550px) {
  .container {
    padding: 0 20px;
  }
}
