.works {
  position: relative;
}

.works__wrap {
  max-width: 1271px;
  background: #faf1e7;
  margin: 0 auto;
  padding-top: 74px;
  padding-bottom: 238px;
  position: relative;
  z-index: 0;
}

.works-title {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 80px;
  line-height: 148%;
  letter-spacing: -0.02em;
  color: #5e534a;
  margin-top: 2px;
  text-align: center;
}

.works__subtitle {
  max-width: 593px;
  margin: 0 auto;
  text-align: center;
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 141%;
  text-align: center;
  color: #99928a;
}

.works-slider {
  z-index: 0;
  margin-top: -174px;
  position: relative;
  .swiper-slide {
    width: 447px;
    margin-top: 255px;
    height: 655px;
    transition: margin-top 0.3s ease;
    img {
      height: 100%;
      object-fit: cover;
    }
  }
  .works-slide {
    z-index: 2;
    width: 100%;
    height: 100%;
    transition: margin-top 0.3s ease;
    .swiper-slide-active {
      width: 580px;
    }
    .swiper-slide-active img {
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  .swiper-slide-active {
    z-index: 3;
    transition: margin-top 0.2s ease;
    top: 0;
    // width: 581px;
    height: 721px;
    margin-top: 0;
  }
  .swiper-slide-prev,
  .swiper-slide-next {
    margin-top: 174px;
    transition: margin-top 0.2s ease;
  }
}

.works-arrows {
  max-width: 840px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  display: flex;
  justify-content: space-between;

  .works-button-next {
    &::before {
      background-image: url('../img/diplom-arr-prev.svg');
    }
  }

  .works-button-prev {
    &::before {
      background-image: url('../img/diplom-arr-next.svg');
    }
  }

  .works-button-next {
    width: 76px;
    height: 76px;
    border-radius: 50%;
    background-color: #fff;
    transition: all 0.3s ease;
    &:hover {
      background: #7db4a1;
    }
    &::before {
      content: '';
      left: 34px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      background-repeat: no-repeat;
      background-size: cover;
      width: 64px;
      height: 7px;
    }
  }

  .works-button-prev {
    width: 76px;
    height: 76px;
    border-radius: 50%;
    background-color: #fff;
    transition: all 0.3s ease;
    &:hover {
      background: #7db4a1;
    }
    &::before {
      background-repeat: no-repeat;
      right: 34px;
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      background-size: cover;
      width: 64px;
      height: 6px;
    }
  }
}

.works-btn {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  bottom: 82px;
  padding: 20px 87px 17px 90px;
  background: #e6bca3;
}
@media screen and (max-width: 1200px) {
  .works__wrap {
    padding-top: 30px;
  }
  .works-title {
    font-size: 60px;
  }

  .works__subtitle {
    font-size: 16px;
  }

  .works-slider .swiper-slide-active {
    height: 510px;
  }

  .works-slider .swiper-slide-prev,
  .works-slider .swiper-slide-next {
    margin-top: 110px;
  }

  .works-slider .swiper-slide {
    height: 500px;
    width: 390px;
  }

  .works-btn {
    bottom: 100px;
  }
}

@media screen and (max-width: 1024px) {
  .works-title {
    font-size: 48px;
  }

  .works__subtitle {
    font-size: 14px;
    margin: 10px auto 0px;
  }

  .works-slider {
    margin-top: -190px;
  }

  .works-slider .swiper-slide {
    height: 380px;
    width: 220px;
  }

  .works-slider .swiper-slide-active {
    height: 400px;
    margin-top: 0 !important;
  }

  .works-arrows .works-button-next {
    width: 60px;
    height: 60px;
  }
  .works-arrows .works-button-prev {
    width: 60px;
    height: 60px;
  }

  .works-arrows .works-button-next::before {
    width: 50px;
    height: 5px;
  }

  .works-arrows .works-button-prev::before {
    width: 50px;
    height: 5px;
  }

  .works-slider .swiper-slide-prev,
  .works-slider .swiper-slide-next {
    margin-top: 80px !important;
  }

  .works-slider .swiper-slide {
    margin-top: 140px;
  }

  .works-btn {
    bottom: 30px;
    z-index: 0;
  }
}

@media screen and (max-width: 768px) {
  .works-slider .works-slide {
    width: 278px;
    margin: 0 auto;
  }

  .works-arrows {
    z-index: 2;
    bottom: 45px;
    top: auto;
    max-width: 280px;
    width: 100%;
    height: 55px;
  }

  .works-arrows .works-button-next {
    width: 55px;
    height: 55px;
    background: #faf1e7;
  }

  .works-arrows .works-button-prev {
    width: 55px;
    height: 55px;
    background: #faf1e7;
  }

  .works-arrows .works-button-prev::before {
    top: 27px;
    right: 22px;
  }

  .works-arrows .works-button-next::before {
    top: 27px;
    left: 22px;
  }
  .works-btn {
    width: 280px;
    bottom: -21px;
  }

  .works-pagination.swiper-pagination {
    display: block;
    bottom: 67px !important;
    position: relative;
    z-index: 1;
  }

  .swiper-pagination-bullets .swiper-pagination-bullet {
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background: #c4c4c4;
    margin: 0 8px !important;
  }

  .works-pagination .swiper-pagination-bullet-active {
    position: relative;
    &:before {
      content: '';
      border-radius: 50%;
      position: absolute;
      width: 16px;
      height: 16px;
      top: -6px;
      left: -6px;
      border: 1px solid #bababa;
    }
  }

  .works-title {
    font-size: 32px;
  }

  .works__subtitle {
    font-size: 14px;
  }

  .works-slider {
    margin-top: -210px;
  }
}

@media screen and (max-width: 425px) {
  .works__wrap {
    padding-top: 44px;
  }

  .works__subtitle {
    max-width: 212px;
    margin: 4px auto 0;
    line-height: 155%;
  }

  .works-btn {
    font-size: 12px;
    line-height: 161%;
    padding: 15px 10px 14px;
    color: #ffffff;

    letter-spacing: 0.175em;
  }
}
