.header {
  padding: 30px 0 30px;
  position: fixed;
  z-index: 4;
  background-color: #ffffff;
  top: 0;
  width: 100%;
  left: 0;
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__menu {
  padding-top: 5px;
  padding-left: 64px;
  z-index: 4;
  flex: 0 0 387px;
  position: relative;
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 161%;
  letter-spacing: 0.695em;
  text-transform: uppercase;
  color: #5e534a;
  cursor: pointer;

  &:before,
  &:after {
    content: '';
    position: absolute;
    left: 0;
    width: 23px;
    height: 1px;
    background: #5e534a;
    transition: all 0.3s ease;
  }

  &:before {
    top: 15px;
  }

  &:after {
    bottom: 11px;
  }

  &.active {
    &:before {
      top: 20px;
      transform: rotate(45deg);
    }

    &:after {
      bottom: 16px;
      transform: rotate(-225deg);
    }
  }
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;

  .logo__name {
    font-family: 'Cormorant', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 119%;
    letter-spacing: 0.33em;
    text-transform: uppercase;
    color: #5d534a;
  }

  .logo__title {
    font-family: 'MuseoSans', sans-serif;
    margin-top: 1px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.025em;
    text-transform: uppercase;
    color: #ebdddd;
    position: relative;
    width: 100%;
    text-align: center;

    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 44px;
      height: 1px;
      background: #ebdddd;
    }

    &:before {
      left: 0;
    }

    &:after {
      right: 0;
    }
  }
}

.header__socials {
  display: flex;
  align-items: center;
}

.header__social {
  width: 39px;
  height: 39px;
  border-radius: 50%;
  border: 1px solid #ebebeb;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 22px;
  transition: all 0.3s ease;

  &:last-child {
    margin-right: 0;
    svg {
      left: 3px;
      margin-left: -3px;
      margin-top: -1px;
    }
  }

  svg path {
    transition: all 0.3s ease;
  }

  &:hover {
    background-color: #e6bca3;
    border: 1px solid #e6bca3;

    svg path {
      fill: #fff;
    }
  }
}

.header__phone {
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 150%;
  color: #59544d;
}

.header__right {
  margin-top: -13px;
  display: flex;
  flex: 0 0 387px;
  align-items: center;
  justify-content: space-between;
}

.header-menu {
  position: fixed;
  top: -100%;
  width: 100%;
  background-color: #ffffff;
  box-shadow: 0px -11px 76px rgba(172, 172, 172, 0.25);
  padding: 100px 0 80px;
  z-index: 2;
  transition: top 0.3s ease;

  &.active {
    top: 108px;
  }
}

.header-menu__wrap {
  display: flex;
  align-items: flex-start;

  .divider {
    height: 199px;
    width: 1px;
    background: #dadada;
  }
}

.header-ul {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  max-width: 591px;
  width: 100%;
  margin-right: 174px;
}

.header-li {
  margin-bottom: 43px;
  width: 50%;
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 148%;
  letter-spacing: 0.34em;
  text-transform: uppercase;
  color: #5e534a;
  transition: all 0.3s ease;
  &:hover {
    color: #7db4a1;
  }
}

.header-contacts {
  margin-left: 92px;

  p {
    font-family: 'Cormorant', sans-serif;
    font-size: 18px;
    line-height: 148%;
    letter-spacing: 0.34em;
    text-transform: uppercase;
    color: #5e534a;
  }

  .header-menu__phone {
    font-family: 'MuseoSans', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 150%;
    color: #59544d;
    position: relative;
    display: block;
    padding-left: 25px;
    margin-top: 31px;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 4px;
      width: 18px;
      height: 18px;
      background-image: url('../img/phone.svg');
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  .header-menu__email {
    font-family: 'MuseoSans', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 150%;
    color: #59544d;
    position: relative;
    display: block;
    padding-left: 25px;
    margin-top: 31px;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 6px;
      background-image: url('../img/email.svg');
      background-repeat: no-repeat;
      background-size: cover;
      width: 18px;
      height: 12px;
    }
  }
}

.header__socials-mob {
  display: none;
}

@media screen and (max-width: 1440px) {
}

@media screen and (max-width: 1200px) {
  .header {
    padding: 20px 0 20px;
  }

  .header__socials {
    display: none;
  }

  .header__wrap {
    align-items: center;
  }

  .header-menu.active {
    top: 87px;
  }

  .header__right {
    justify-content: flex-end;
  }

  .header__phone {
    font-weight: 400;
    font-size: 20px;
  }

  .header-menu {
    padding: 40px 0 50px;
  }

  .header-li {
    font-size: 16px;
    margin-bottom: 20px;
  }

  .header-contacts p {
    font-size: 16px;
    margin-bottom: 20px;
  }

  .header-contacts .header-menu__phone {
    font-size: 16px;
    margin-top: 20px;
  }

  .header-contacts .header-menu__email {
    font-size: 16px;
    margin-top: 20px;
  }

  .header-ul {
    max-width: 490px;
    margin-right: 60px;
  }

  .header-menu__wrap .divider {
    height: 130px;
  }

  .header__right {
    flex: 1 0 0;
  }

  .header__menu {
    flex: 1 0 0;
    padding-top: 0;
    padding-left: 40px;
  }

  .header__menu:before {
    top: 12px;
  }

  .header__wrap {
    justify-content: space-between;
  }

  .header__menu.active:before {
    top: 15px;
  }

  .header__logo.logo {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);

    .logo__name {
      font-size: 18px;
      letter-spacing: 6px;
    }

    .logo__title {
      font-size: 12px;
      margin-top: 2px;
    }
  }

  .header-menu.active {
    top: 72px;
  }
}

@media screen and (max-width: 1024px) {
  .header__menu {
    font-size: 16px;
    letter-spacing: 5px;
  }
  .header__menu:before {
    top: 7px;
  }

  .header__menu:after {
    bottom: 9px;
  }
  .header__wrap {
    align-items: baseline;
  }

  .header__phone {
    font-size: 16px;
  }

  .header-menu.active {
    top: 65px;
  }

  .header__menu.active:before {
    top: 13px;
  }

  .header__menu.active:after {
    bottom: 12px;
  }

  .header-ul {
    max-width: 380px;
  }

  .header-li {
    font-size: 14px;
    margin-bottom: 15px;
  }

  .header-contacts p {
    font-size: 14px;
  }

  .header-contacts .header-menu__phone {
    font-size: 14px;
  }

  .header-contacts .header-menu__phone:before {
    top: 3px;
    width: 14px;
    height: 14px;
  }
  .header-contacts .header-menu__email:before {
    width: 16px;
    height: 11px;
  }

  .header-contacts .header-menu__email {
    margin-top: 10px;
  }

  .header-menu {
    padding: 30px 0 30px;
  }
}

@media screen and (max-width: 768px) {
  .header__socials-mob {
    display: flex;
    margin-top: 23px;
  }
  .header {
    box-shadow: 0px 4px 38px rgba(221, 211, 200, 0.25);
  }

  .header__logo.logo .logo__name {
    font-size: 12px;
    letter-spacing: 2px;
  }

  .header__logo.logo .logo__title {
    font-size: 6px;
    margin-top: 0px;
  }

  .header-menu.active {
    top: 51px;
  }

  .logo .logo__title:before,
  .logo .logo__title:after {
    width: 29px;
  }

  .header__wrap {
    align-items: center;
  }

  .header__phone {
    font-size: 0;
    width: 29px;
    border-radius: 50%;
    height: 29px;
    background: #faf1e7;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 8px;
      left: 8px;
      background-image: url('../img/phone.svg');
      width: 12px;
      height: 12px;
      background-size: cover;
    }
  }

  .header__menu {
    font-size: 0;
    height: 6px;
    width: 16px;

    &:before,
    &:after {
      width: 16px;
    }
  }

  .header__right {
    margin-top: 0;
  }

  .header__logo.logo {
    top: 14px;
  }

  .header__menu.active:before {
    top: 2px;
  }

  .header__menu.active:after {
    bottom: 3px;
  }

  .header-menu__wrap {
    flex-direction: column;
    align-items: flex-start;
  }

  .header-ul {
    margin-right: 0;
    flex-direction: column;
    max-width: none;
    width: auto;
  }

  .header-li {
    width: auto;
    font-size: 16px;
    line-height: 125%;
    margin-bottom: 30px;
  }

  .header-contacts p {
    font-size: 16px;
    line-height: 125%;
  }

  .header-contacts .header-menu__phone {
    margin-top: 30px;
  }

  .header-contacts {
    margin-left: 0;
    margin-top: 23px;
  }

  .header-menu__wrap .divider {
    height: 1px;
    width: 252px;
  }

  .header-contacts .header-menu__phone {
    font-size: 18px;
    line-height: 150%;
    padding-left: 26px;
  }

  .header-contacts .header-menu__email {
    font-size: 18px;
    line-height: 150%;
    padding-left: 26px;
  }

  .header-contacts .header-menu__phone:before {
    width: 18px;
    height: 18px;
  }

  .header-contacts .header-menu__email:before {
    width: 19px;
    height: 13px;
  }
}
