.beauty {
  padding-top: 93px;

  .container {
    background: url('../img/beauty-title.png') right 14px top 47px no-repeat;
    position: relative;
    height: 474px;
  }
}

.beauty-img {
  position: absolute;
  max-width: 384px;
  border: 30px solid #fff;
  img {
    display: block;
  }
}

.beauty-title {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 80px;
  line-height: 103.5%;
  max-width: 817px;
  top: 324px;
  left: 473px;
  position: absolute;
  letter-spacing: -0.02em;
  color: #5e534a;
}

.beauty__wrap {
  margin-top: -213px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.beauty-item {
  width: 50%;
  padding-left: 110px;
  padding-top: 78px;

  &:nth-child(1) {
    padding-top: 258px;
    background: #faf1e7;
    padding-left: 300px;

    .beauty-item__num {
      color: #fff;
    }

    .beauty-item__text {
      padding-bottom: 109px;
    }
  }

  &:nth-child(2) {
    padding-top: 258px;
    padding-left: 100px;

    .beauty-item__text {
      padding-bottom: 109px;
    }
  }

  &:nth-child(3) {
    padding-left: 310px;

    .beauty-item__text {
      padding-bottom: 162px;
    }
  }

  &:nth-child(4) {
    background: #faf1e7;

    .beauty-item__num {
      color: #fff;
    }

    .beauty-item__text {
      padding-bottom: 162px;
    }
  }

  &:nth-child(5) {
    padding-left: 310px;

    background: #faf1e7;

    .beauty-item__num {
      color: #fff;
    }

    .beauty-item__text {
      padding-bottom: 143px;
    }
  }

  &:nth-child(6) {
    .beauty-item__text {
      padding-bottom: 100px;
    }
  }
}

.beauty-item__num {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 82px;
  line-height: 152%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #f6ebdd;
}

.beauty-item__title {
  padding-top: 10px;
  font-family: 'Cormorant', sans-serif;
  font-weight: 400;
  font-size: 37px;
  line-height: 36px;
  letter-spacing: -0.02em;
  color: #6c6c6c;
  max-width: 546px;
}

.beauty-item__text {
  margin-top: 17px;
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 170.5%;
  color: #787878;
  max-width: 546px;
}

@media screen and (max-width: 1440px) {
  .beauty-item:nth-child(1) {
    padding-left: 140px;
  }

  .beauty-item:nth-child(3) {
    padding-left: 140px;
  }

  .beauty-item:nth-child(5) {
    padding-left: 140px;
  }
}

