  .beauty-item:nth-child(3) .beauty-item__text {
    padding-bottom: 60px;
  }

  .beauty-item:nth-child(4) .beauty-item__text {
    padding-bottom: 60px;
  }

  .beauty-item:nth-child(5) .beauty-item__text {
    padding-bottom: 60px;
  }

  .beauty-item:nth-child(6) .beauty-item__text {
    padding-bottom: 60px;
  }
}

