    height: 380px;
    background-size: 710px;
  }

  .beauty-title {
    font-size: 42px;
    left: 340px;
    top: 200px;
    max-width: 300px;
  }

  .beauty-img {
    position: absolute;
    max-width: 300px;
    border: 20px solid #fff;
