  }
}

@media screen and (max-width: 768px) {
  .beauty {
    padding-top: 44px;
  }

  .beauty .container {
    background: none;
  }

  .beauty-img {
    position: static;
    margin: 0 auto;
  }

  .beauty-title {
    position: static;
    font-size: 32px;
    line-height: 91.5%;
    text-align: center;
    margin: 24px auto 0;
  }

  .beauty__wrap {
    flex-direction: column;
  }

  .beauty-item {
