    }
  }

  .beauty-item:nth-child(5) {
    padding-top: 40px;
    padding-left: 20px;
    padding-bottom: 80px;
    background: #faf1e7;
    .beauty-item__text {
      padding-bottom: 0px;
    }

    .beauty-item__num {
      color: #fff;
      margin-top: 0;
    }
  }

  .beauty-item:nth-child(6) {
    padding-top: 40px;
    padding-left: 20px;
    background: #fff;
    padding-bottom: 30px;
    .beauty-item__text {
      padding-bottom: 0px;
    }

    .beauty-item__num {
      margin-top: 0;
    }
  }
}

@media screen and (max-width: 425px) {
  .beauty-title {
    max-width: 280px;
    margin: 24px auto 0;
  }

  .beauty-item:nth-child(1) .beauty-item__num {
    margin-top: 130px;
  }

  .beauty-item {
    padding-left: 15px;
    padding-right: 15px !important;
  }

