.style {
  padding-top: 116px;
  position: relative;

  .box-scene {
    position: absolute;
    width: 711px;
    height: 698px;
    background: #faf1e7;
    left: 0;
    top: 57px;
  }

  .line-scene {
    position: absolute;
    width: 1px;
    height: 273px;
    background: #efe7db;
    top: -160px;
  }
}

.style__wrap {
  display: flex;
  justify-content: space-around;
}

.style-image {
  position: relative;
  z-index: 1;
  max-width: 548px;
  width: 100%;
}

.style-info {
  margin-top: 170px;
  max-width: 572px;
}

.style-info__suptitle {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 26px;
  line-height: 148%;
  letter-spacing: 0.01em;
  color: #c9b9a5;
}

.style-info__name {
  margin-top: 5px;
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 80px;
  line-height: 96.5%;
  letter-spacing: -0.02em;
  color: #5e534a;
}

.style-info__text {
  margin-top: 27px;
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 170.5%;
  color: #787878;
  padding-left: 95px;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    left: 0;
    width: 60px;
    height: 1px;
    background: #dfdfdf;
    top: 16px;
  }
}

.style-info__link {
  margin-top: 35px;
  padding-left: 96px;
  display: inline-block;
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 161%;
  color: #9bc8b8;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    top: 10px;
    right: -26px;
    background-image: url('../img/green-arrow.svg');
    width: 19px;
    height: 7px;
    background-size: cover;
  }
}

.style-btn {
  display: block;
  margin-top: 60px;
}

@media screen and (max-width: 1440px) {
  .style .box-scene {
    position: absolute;
    width: 530px;
    height: 600px;
  }

  .style-image {
    max-width: 480px;
  }

  .style-info__suptitle {
    font-size: 22px;
  }

  .style-info__name {
    font-size: 70px;
  }

  .style-info__text {
    font-size: 16px;
    padding-left: 55px;
  }

  .style-info__text:before {
    width: 40px;
    top: 13px;
  }

  .style-info__link {
    padding-left: 55px;
  }

  .style-btn {
    margin-top: 40px;
  }
}

@media screen and (max-width: 1200px) {
  .style-info {
    // padding-top: 126px;
    max-width: 420px;
  }

  .style-info__suptitle {
    font-size: 20px;
  }

  .style-info__name {
    font-size: 60px;
  }

  .style-info__link {
    margin-top: 30px;
  }

  .style-image {
    max-width: 420px;
  }
}

@media screen and (max-width: 1024px) {
  .style .box-scene {
    width: 390px;
    height: 490px;
  }

  .style-info {
    max-width: 330px;
  }

  .style .line-scene {
    display: none;
  }
  .style-image {
    max-width: 380px;
  }

  .style-info__name {
    font-size: 40px;
    margin-top: 10px;
  }

  .style-info__suptitle {
    font-size: 18px;
  }

  .style-info__text {
    font-size: 14px;
    margin-top: 20px;
  }

  .style-info__text:before {
    width: 30px;
    top: 11px;
  }

  .style-info__link {
    margin-top: 20px;
    font-size: 14px;
  }
}

@media screen and (max-width: 768px) {
  .style {
    padding-top: 50px;
  }
  .style__wrap {
    flex-direction: column;
    align-items: center;
  }

  .style .box-scene {
    width: 191px;
    height: 338px;
  }
  .style-image {
    max-width: 278px;
    margin: 0 auto;

    img {
      display: block;
    }
  }
  .style-info {
    margin-top: 15px;
    max-width: none;
  }

  .style-info__suptitle {
    font-size: 18px;
    line-height: 148%;
    letter-spacing: 0.01em;
  }

  .style-info__name {
    font-size: 32px;
    line-height: 125%;
    letter-spacing: -0.02em;
    margin-top: 0;
  }

  .style-info__text {
    font-weight: 300;
    font-size: 12px;
    line-height: 155%;
    padding-left: 27px;
    max-width: 238px;
    margin-top: 9px;
  }

  .style-info__text:before {
    width: 16px;
    top: 8px;
  }

  .style-info__link {
    padding-left: 27px;
    margin-top: 10px;
    font-size: 14px;
  }

  .style-btn {
    margin-top: 15px;
  }
}

@media screen and (max-width: 425px) {
  .style {
    padding-top: 51px;
  }

  .style__wrap {
    align-items: flex-start;
  }

  .style-info__text {
    max-width: none;
  }

  .style-btn {
    width: 100%;
    font-size: 12px;
    line-height: 161%;
    letter-spacing: 0.175em;
    text-transform: uppercase;
    color: #787878;
    padding: 15px 10px 14px 10px;
  }
}
