.diplom {
  padding-top: 158px;
  padding-bottom: 221px;
}

.diplom-title {
  text-align: center;
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 80px;
  line-height: 143.5%;
  letter-spacing: -0.02em;
  color: #5e534a;
}

.works-pagination {
  display: none;
}

.diplom-subtitle {
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 161%;
  text-align: center;
  color: #99928a;
  max-width: 420px;
  margin: 0 auto;
}

.diplom__wrap {
  margin-top: 50px;
  position: relative;
}

.diplom-slider {
  .swiper-wrapper {
    height: 454px;
  }
}

.diplom-slide {
  height: 100%;

  .diplom-slide__wrap {
    width: 189px;
    margin: 0 auto;
  }

  transition: all 0.3s ease;

  img {
    object-fit: contain;
    height: 100%;
  }
}

.diplom-slide.swiper-slide-active {
  .diplom-slide__wrap {
    width: 100%;
    margin: 0 auto;
    height: 424px;
  }
}

.diplom-arrows {
  display: flex;
  align-items: center;
  justify-content: space-between;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  max-width: 922px;
  width: 100%;
  top: 316px;
  z-index: 1;

  .diplom-arr-next,
  .diplom-arr-prev {
    width: 104px;
    height: 104px;

    &::before {
      width: 80px;
      height: 9px;
    }
  }

  .diplom-arr-next:before {
    background-image: url('../img/diplom-arr-next.svg');
  }
}

.diplom-pagination {
  bottom: -30px !important;
  align-items: center;
  display: flex;
  left: 50%;
  transform: translateX(-50%);

  .swiper-pagination-bullet {
    width: 7px;
    height: 7px;
    margin: 0 20px !important;
    transition: all 0.2s ease;
  }

  .swiper-pagination-bullet-active {
    width: 10px;
    height: 10px;
    background: #5e534a;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      border: 1px solid #bababa;
      width: 26px;
      height: 26px;
      border-radius: 50%;
      top: -8px;
      left: -8px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .diplom {
    padding-bottom: 60px;
  }
  .diplom-title {
    font-size: 60px;
  }

  .diplom-subtitle {
    font-size: 16px;
  }

  .diplom-slide.swiper-slide-active .diplom-slide__wrap {
    height: 360px;
  }

  .diplom-arrows .diplom-arr-next,
  .diplom-arrows .diplom-arr-prev {
    width: 60px;
    height: 60px;
    transition: all 0.3s ease;
  }

  .diplom-arrows .diplom-arr-next::before,
  .diplom-arrows .diplom-arr-prev::before {
    width: 50px;
    height: 5px;
  }

  .slider-arr-prev:before {
    left: 30px;
  }

  .slider-arr-next:before {
    right: 30px;
  }
}

@media screen and (max-width: 1024px) {
  .diplom {
    margin-top: 0;
    padding-top: 60px;
  }
  .diplom-title {
    font-size: 48px;
  }

  .diplom-arrows {
    top: 200px;
  }

  .diplom-pagination {
    bottom: 35px !important;
  }
}

@media screen and (max-width: 768px) {
  .diplom {
    margin-top: 0;
  }

  .diplom-arrows {
    top: auto;
    bottom: 0;
  }

  .diplom-arr-next,
  .diplom-arr-prev {
    width: 56px !important;
    height: 56px !important;

    &::before {
      width: 45px !important;
      height: 5px !important;
    }
  }

  .diplom-arr-prev::before {
    left: 26px;
  }

  .diplom-arr-next::before {
    right: 26px;
  }

  .diplom-pagination .swiper-pagination-bullet {
    width: 4px;
    height: 4px;
    margin: 0 6px !important;
  }

  .diplom-pagination .swiper-pagination-bullet-active {
    width: 6px;
    height: 6px;

    &:before {
      width: 16px;
      height: 16px;
      top: -6px;
      left: -6px;
    }
  }

  .diplom-arrows {
    bottom: 10px;
  }
}

@media screen and (max-width: 768px) {
  .diplom {
    padding-top: 30px;
    padding-bottom: 55px;
  }
  .diplom-title {
    font-size: 32px;
    line-height: 125%;
  }
  .diplom-subtitle {
    font-size: 12px;
  }

  .diplom__wrap {
    margin-top: 30px;
  }

  .diplom-pagination {
    display: block;
  }
}

@media screen and (max-width: 425px) {
  .diplom-subtitle {
    margin-top: 8px;
  }
}
