.hero {
  margin-top: 131px;

  > .container {
    position: relative;
  }
}

.hero-bg {
  max-width: 924px;
  width: 100%;
}


.hero__wrap {
  display: flex;
  justify-content: space-between;
}

.hero__info {
  margin-top: 131px;
}

.hero__info_suptitle {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 187.5%;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #88B9A8;
  padding-left: 32px;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    top: 13px;
    left: 0;
    width: 20px;
    background: #88b9a8;
    height: 1px;
  }
}

.hero__info_title {
  margin-top: 25px;
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 87px;
  line-height: 94.5%;
  letter-spacing: -0.02em;
  color: #5E534A;
}

.hero__info_subtitle {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 26px;
  line-height: 148%;
  letter-spacing: -0.005em;
  color: #88B9A8;
  max-width: 338px;
  margin-top: 11px;

  span {
    font-family: 'MuseoSans', sans-serif;
    font-size: 26px;
    color: #5e534a;
    font-weight: 600;
  }

  .oldprice {
    font-family: 'MuseoSans', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 148%;
    letter-spacing: 0.025em;
    text-transform: uppercase;
    color: #B4AAA2;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      background: #9BC8B8;
      transform: rotate(-5.02deg);
      width: 100%;
      height: 1px;
      top: 11px;
    }
  }
}

.hero-btn {
  margin-top: 69px;
}


.hero-bg__text {
  position: absolute;
  bottom: 19px;
  right: 0;
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 22px;
  line-height: 143.5%;
  letter-spacing: -0.005em;
  text-transform: uppercase;
  color: #9F9F9F;
  max-width: 261px;

  &:before {
    content: '';
    position: absolute;
    top: -22px;
    left: 0;
    width: 45px;
    height: 1px;
    background: #aba59c;
  }
}

@media screen and (max-width: 1440px) {
  .hero__wrap {
    align-items: center;
    justify-content: space-evenly;
  }
  .hero-bg {
    width: 700px;
  }

  .hero__info {
    margin-top: 0;
  }

  .hero__info_suptitle {
    font-size: 14px;
  }

  .hero__info_title {
    font-size: 74px;
  }

  .hero__info_subtitle {
    font-size: 24px;
  }

  .hero-bg__text {
    font-size: 18px;
    max-width: 221px;
  }
}

@media screen and (max-width: 1200px) {
  .hero-bg {
    max-width: 550px;
  }

  .hero__info_title {
    font-size: 64px;
  }

  .hero-btn {
    margin-top: 40px;
  }

  .hero-bg__text {
    bottom: 0;
    font-size: 16px;
    max-width: 190px;
    right: 40px;
  }
}

@media screen and (max-width: 1024px) {
  .hero-bg {
    max-width: 400px;
  }

  .hero__info_title {
    font-size: 42px;
  }

  .hero__info_suptitle {
    font-size: 12px;
  }

  .hero__info_suptitle:before {
    top: 10px;
  }

  .hero__info_subtitle {
    font-size: 18px;
    max-width: 268px;
  }

  .hero__info_subtitle span {
    font-size: 18px;
  }

  .hero__info_subtitle .oldprice {
    font-size: 16px;
  }

  .hero-btn {
    margin-top: 20px;
  }
  .hero-bg__text {
    font-size: 14px;
    max-width: 170px;
  }

  .hero-bg__text:before {
    top: -15px;
    width: 30px;
  }
}


@media screen and (max-width: 768px) {
  .hero__info_subtitle .oldprice:before {
    top: 6px;
  }
  .header {
    padding: 11px 0 11px;
  }
  .hero__wrap {
    flex-direction: column-reverse;
    align-items: center;
  }

  .hero__info {
    text-align: center;
    margin-top: 14px;
  }

  .hero-bg__text {
    display: none;
  }

  .hero__info_suptitle:before {
    display: none;
  }

  .hero__info_suptitle {
    font-size: 12px;
    line-height: 187.5%;
    padding-left: 0;
  }

  .hero__info_title {
    margin-top: 4px;
    font-size: 37px;
    line-height: 88%;
  }

  .hero__info_subtitle {
    text-transform: uppercase;
    font-size: 12px;

    span {
      font-size: 12px;
    }

    .oldprice {
      font-size: 12px;
    }
  }


}


@media screen and (max-width: 475px) {
  .hero {
    margin-top: 70px;
  }
  .hero-bg {
    max-width: 100%;


  }

  .hero-btn {
    font-size: 12px;
    line-height: 161%;
    padding: 15px 10px 14px 10px;
    width: 100%;
    margin-top: 23px;
    letter-spacing: 0.175em;
    font-weight: 300;
  }

  .hero__info_subtitle {
    letter-spacing: 1px;
    margin-top: 15px;
    font-weight: 600;
    line-height: 22px;
  }
}