.reviews {
  padding-top: 133px;
  padding-bottom: 142px;
}

.reviews-slider {
  margin-top: 35px;
}

.reviews__title {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 80px;
  line-height: 143.5%;
  letter-spacing: -0.02em;
  text-align: center;
}

.reviews__slide {
  padding: 55px 76px 86px;
  text-align: center;
  max-width: 786px;
  margin: 0 auto 130px;
  background: #faf1e7;
  position: relative;
}

.reviews__text {
  margin-top: 31px;
  font-family: 'Cormorant', sans-serif;
  font-style: italic;
  font-weight: normal;
  font-size: 26px;
  line-height: 136.5%;
  text-align: center;
  letter-spacing: 0.03em;
  color: #918a85;
}

.reviews__link {
  margin-top: 50px;
  display: block;
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 141%;
  text-align: center;
  text-decoration-line: underline;
  color: #9bc8b8;
}

.reviews__avatar {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -110px;
  img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    filter: drop-shadow(0px 16px 17px #e7dbcf);
  }

  p {
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 141%;
    text-align: center;
    color: #9bc8b8;
  }
}

.reviews__avatar_link {
  font-family: 'MuseoSans', sans-serif;
  margin-top: 5px;
  font-weight: 300;
  font-size: 16px;
  line-height: 141%;
  display: inline-block;
  color: #99928a;
  position: relative;
  padding-left: 29px;
  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background-image: url('../img/ireviews-ig.svg');
    width: 20px;
    height: 20px;
  }
}

.reviews-arrows {
  .reviews-button-next,
  .reviews-button-prev {
    width: 75px;
    height: 75px;
    border-radius: 50%;
    background: #faf1e7;
    transition: all 0.3s ease;
    &:hover {
      background: #9bc8b8;
    }
  }

  .reviews-button-next {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      background-image: url('../img/reviews-arr-r.svg');
      width: 57px;
      height: 7px;
      top: 50%;
      transform: translateY(-50%);
      left: -18px;
    }
  }

  .reviews-button-prev {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      background-image: url('../img/reviews-arr-l.svg');
      width: 57px;
      height: 7px;
      top: 50%;
      transform: translateY(-50%);
      right: -18px;
    }
  }
}

.reviews-arrows {
  position: absolute;
  max-width: 1356px;
  width: 100%;
  top: 100px;
  display: flex;
  justify-content: space-between;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}

.reviews-pagination.swiper-pagination {
  bottom: 20px;
  display: none;
}

@media screen and (max-width: 1200px) {
  .reviews {
    padding-top: 30px;
    padding-bottom: 50px;
  }

  .reviews__title {
    font-size: 60px;
  }

  .reviews__slide {
    padding: 40px 50px 50px;
    max-width: 630px;
  }

  .reviews__text {
    margin-top: 30px;
    font-size: 22px;
  }

  .reviews__link {
    margin-top: 20px;
  }

  .reviews__avatar p {
    font-size: 18px;
  }

  .reviews-arrows {
    max-width: 1060px;
  }
}

@media screen and (max-width: 1024px) {
  .reviews__title {
    font-size: 48px;
  }

  .reviews__text {
    margin-top: 20px;
  }

  .reviews__avatar_link {
    font-size: 14px;
  }

  .reviews__avatar_link::before {
    width: 18px;
    height: 18px;
    background-size: cover;
  }
}

@media screen and (max-width: 768px) {
  .reviews {
    padding-top: 80px;
  }

  .reviews-slider {
    margin-top: 15px;
  }
  .reviews-arrows {
    max-width: none;
    position: static;
    transform: none;
  }

  .reviews-arrows .reviews-button-next,
  .reviews-arrows .reviews-button-prev {
    width: 55px;
    height: 55px;
  }

  .reviews-arrows .reviews-button-next::before {
    background-size: cover;
    width: 50px;
    height: 5px;
  }

  .reviews-arrows .reviews-button-prev::before {
    background-size: cover;
    width: 50px;
    height: 5px;
  }

  .reviews-pagination.swiper-pagination {
    display: block;
    bottom: 20px;
    z-index: 0;
  }

  .reviews-pagination .swiper-pagination-bullet-active {
    position: relative;
    &:before {
      content: '';
      border-radius: 50%;
      position: absolute;
      width: 16px;
      height: 16px;
      top: -6px;
      left: -6px;
      border: 1px solid #bababa;
    }
  }

  .reviews__text {
    font-size: 16px;
  }

  .reviews__link {
    font-size: 12px;
  }

  .reviews__slide {
    padding: 46px 37px 68px;
  }

  .reviews__title {
    font-size: 32px;
  }
}

@media screen and (max-width: 425px) {
  .reviews {
    padding-top: 84px;
  }

  .reviews-slider {
    margin-top: 20px;
  }

  .reviews__avatar {
    bottom: -80px;
    width: 100%;
  }

  .reviews__link {
    margin-top: 27px;
  }

  .reviews__slide {
    margin: 0 auto 93px;
  }

  .reviews__text {
    margin-top: 23px;
  }

  .reviews__avatar img {
    width: 94px;
    height: 94px;
  }

  .reviews__avatar p {
    font-size: 18px;
    line-height: 141%;
  }

  .reviews__slide {
    padding: 46px 37px 70px;
  }
}
