.services {
  padding-top: 124px;
}

.services-title {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 80px;
  line-height: 103.5%;
  letter-spacing: -0.02em;
  color: #5e534a;
}

.services__wrap {
  margin-top: 58px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.services-item {
  background: #faf1e7;
  max-width: 717px;
  width: 100%;
  padding: 24px 40px 34px 39px;
  margin-bottom: 42px;
}

.services-item__title {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 37px;
  line-height: 161%;
  letter-spacing: -0.02em;
  color: #c9b9a5;
}

.services-item__price_wrap {
  display: flex;
  align-items: center;
}

.services-item__price_old {
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 148%;
  letter-spacing: 0.025em;
  text-transform: uppercase;
  color: #b4aaa2;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    top: 13px;
    background: #9bc8b8;
    transform: rotate(-5.02deg);
  }
}

.services-item__price {
  margin-left: 20px;
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 161%;
  letter-spacing: 0.03em;
  color: #414141;
}

.services-item__footer {
  margin-top: 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &.active {
    padding-bottom: 30px;
  }
}

.services-item__more {
  font-family: 'MuseoSans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 161%;
  color: #9bc8b8;
  cursor: pointer;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    right: -28px;
    top: 11px;
    background-image: url(../img/green-arrow.svg);
    width: 16px;
    height: 7px;
    background-size: cover;
  }
}

.services-item__btn {
  font-family: 'MuseoSans', sans-serif;
  padding: 15px 58px 14px 57px;
  background: #e6bca3;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 187.5%;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #ffffff;
}

.services-item__content {
  border-top: 1px solid #ffffff;
  max-height: 0;
  height: 0;
  overflow: hidden;
  opacity: 0;
  transition: all 0.8s ease;

  h4 {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 200%;
    color: #787878;
  }

  ul {
    margin-bottom: 18px;
  }

  li {
    font-weight: 100;
    margin-top: 10px;
    font-size: 18px;
    color: #787878;
  }

  &.active {
    max-height: none;
    opacity: 1;
    height: auto;
    padding-top: 27px;
  }
}

@media screen and (max-width: 1500px) {
  .services-item {
    max-width: 49%;
  }
}

@media screen and (max-width: 1200px) {
  .services {
    padding-top: 90px;
  }
  .services-title {
    font-size: 60px;
  }

  .services-item {
    max-width: 49%;
    margin-bottom: 30px;
    padding: 24px 40px 26px 39px;
  }

  .services-item__title {
    font-size: 32px;
  }

  .services-item__btn {
    font-size: 12px;
    padding: 11px 35px 10px 35px;
  }

  .services-item__price_old {
    font-size: 18px;
  }

  .services-item__price {
    font-size: 18px;
    margin-left: 12px;
  }

  .services-item__more {
    font-size: 16px;
  }
}

@media screen and (max-width: 1024px) {
  .services {
    padding-top: 60px;
  }

  .services-title {
    font-size: 48px;
  }

  .services__wrap {
    margin-top: 30px;
  }

  .services-item {
    padding: 10px 30px 20px;
  }

  .services-item__title {
    font-size: 28px;
  }

  .services-item__price_old {
    font-size: 16px;
  }

  .services-item__price {
    font-size: 16px;
    margin-left: 12px;
  }

  .services-item__btn {
    font-size: 12px;
    padding: 11px 25px 10px 25px;
  }

  .services-item__content h4 {
    font-size: 15px;
  }

  .services-item__content li {
    font-size: 14px;
    margin-top: 7px;
  }
}

@media screen and (max-width: 768px) {
  .services {
    padding-top: 50px;
  }
  .services__wrap {
    flex-direction: column;
    align-items: center;
  }

  .services-title {
    font-size: 32px;
  }

  .services-item {
    padding: 13px 19px;
    width: 100%;
    max-width: 100%;
    margin-bottom: 18px;
  }

  .services-item__title {
    font-size: 22px;
  }

  .services-item__more {
    font-size: 14px;

    &:before {
      top: 8px;
    }
  }
}

@media screen and (max-width: 425px) {
  .services {
    padding-top: 64px;
  }
  .services-item__footer {
    flex-direction: column;
    margin-top: 6px;
    align-items: flex-start;
  }

  .services-item {
    padding: 10px 21px 19px;
  }

  .services-item__btn {
    margin-top: 21px;
    width: 100%;
    padding: 13px 25px 13px 25px;
    font-weight: 300;
  }

  .services__wrap {
    margin-top: 22px;
  }

  .services-item__title {
    font-size: 22px;
    line-height: 161%;
  }
}
