.findme {
  background: url('../img/find-me-bg.svg') center no-repeat, #faf1e7;
  padding-top: 76px;
  padding-bottom: 93px;
}

.findme__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.findme__img {
  width: 86px;
  margin: 0 auto;
}

.findme_text {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 80px;
  line-height: 87.5%;
  text-align: center;
  letter-spacing: -0.02em;
  color: #5e534a;
  max-width: 903px;
  margin: 30px auto 0;

  a {
    color: #dcc9b3;
  }
}

.findme__link {
  padding: 15px 68px;
  margin-top: 65px;
  background-color: #fff;
}

@media screen and (max-width: 1200px) {
  .findme {
    padding-top: 40px;
    padding-bottom: 60px;
  }
  .findme_text {
    font-size: 50px;
    margin: 30px auto 0;
  }

  .findme__link {
    margin-top: 40px;
  }
}

@media screen and (max-width: 1024px) {
  .findme {
    padding-bottom: 40px;
  }

  .findme__img {
    width: 50px;
  }

  .findme_text {
    font-size: 36px;
    margin: 20px auto 0;
  }

  .findme__link {
    margin-top: 30px;
  }
}

@media screen and (max-width: 768px) {
  .findme {
    padding-bottom: 77px;
  }
  .findme_text {
    font-size: 32px;
  }

  .findme__img {
    width: 54px;
  }

  .findme__link {
    margin-top: 20px;
    font-size: 12px;
    padding: 15px 38px 14px 50px;
  }
}

@media screen and (max-width: 425px) {
  .findme {
    padding-top: 48px;
  }

  .findme_text {
    margin: 28px auto 0;
    line-height: 112.5%;
  }
}
