/**************************
  Basic Modal Styles
**************************/

// Э

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(218, 200, 189, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.modal__container {
  background: #faf1e7;
  padding: 74px 78px 87px 89px;
  max-width: 961px;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  box-sizing: border-box;

  .choices__list--dropdown {
    background: #faf1e7;
    border: none;
  }

  .form-textarea {
    width: 422px;
    resize: none;
  }

  .form-row {
    margin-top: 18px;

    &:nth-child(2) {
      margin-top: 89px;
    }

    .choices {
      width: 323px;
    }

    input[type='text'] {
      width: 323px;
    }

    input[type='tel'] {
      width: 190px;
    }

    input[type='email'] {
      width: 190px;
    }
  }
}

@supports (display: flex) {
  .modal__container {
    height: initial;
    max-height: 80vh;
  }
}

.modal__header {
  position: relative;
  display: block;
  height: 30px;
  margin-bottom: 20px;
}

@supports (display: flex) {
  .modal__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: initial;
    margin-bottom: 0px;
  }
}

.modal__title {
  position: absolute;
  top: 20px;
  left: 20px;
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.25;
  color: #00449e;
  box-sizing: border-box;
}

.modal__close {
  position: absolute !important;
  top: -57px;
  right: -43px;
  width: 18px;
  height: 18px;
  background: transparent;
  border: 0;
  cursor: pointer;
  margin: 0px;
  padding: 0px;
}

.modal__subtitle {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  color: #5e534a;
  margin-top: 62px;
}

@supports (display: flex) {
  .modal__title {
    position: static;
  }
}

.modal__header .modal__close:before {
  content: '';
  position: absolute;
  background-image: url('../img/close.svg');
  width: 18px;
  height: 18px;
}

.modal__content {
  margin-top: 10px;
  margin-bottom: 10px;
  color: rgba(0, 0, 0, 0.8);
}

@supports (display: flex) {
  .modal__content {
    margin-top: 2rem;
    margin-bottom: 2rem;
    line-height: 1.5;
  }
}

.modal__btn {
  font-size: 0.875rem;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: #e6e6e6;
  color: rgba(0, 0, 0, 0.8);
  border-radius: 0.25rem;
  border-style: none;
  border-width: 0;
  cursor: pointer;
  -webkit-appearance: button;
  text-transform: none;
  overflow: visible;
  line-height: 1.15;
  margin: 0;
  will-change: transform;
  -moz-osx-font-smoothing: grayscale;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.modal__btn-primary {
  background-color: #00449e;
  color: #fff;
}

/**************************
  Demo Animation Style
**************************/

@keyframes mmfadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes mmfadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes mmslideIn {
  from {
    transform: translateY(15%);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes mmslideOut {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-10%);
  }
}

.micromodal-slide {
  display: none;
}

.micromodal-slide.is-open {
  display: block;
}

.micromodal-slide[aria-hidden='false'] .modal__overlay {
  animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden='false'] .modal__container {
  animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden='true'] .modal__overlay {
  animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden='true'] .modal__container {
  animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide .modal__container,
.micromodal-slide .modal__overlay {
  will-change: transform;
}

/**************************
  Custom styles for individual modals
**************************/

.modal__container button {
  outline: none;
  cursor: pointer !important;
}

.modal__container h2.modal__title {
  text-align: center;
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 143.5%;
  letter-spacing: 0.255em;
  text-transform: uppercase;
  color: #5e534a;
}

.modal__header {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.25;
  color: #00449e;
  box-sizing: border-box;
}

.modal__content {
  margin-top: 2rem;
  margin-bottom: 2rem;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.8);
}

.modal__btn {
  padding: 10px 15px;
  background-color: #e6e6e6;
  border-radius: 4px;
  -webkit-appearance: none;
}

.modal-btn.form-btn {
  background: #e6bca3;
  padding: 13px 127px 12px 22px;
  color: #ffffff;
  font-size: 14px;
  line-height: 161%;
  letter-spacing: 0.175em;
  text-transform: uppercase;
  font-weight: 300;

  &:before {
    content: '';
    position: absolute;
    right: 22px;
    top: 50%;
    transform: translateY(-50%);
    background-image: url('../img/form-btn-arr-white.svg');
    width: 16px;
    height: 8px;
  }
}

.modal__btn_wrap {
  display: flex;
  align-items: baseline;

  justify-content: space-between;
}

.modal-contacts {
  display: flex;
  align-items: center;
  background: #f9e9d7;
  border-radius: 10px;
  padding: 15px 32px 15px 27px;
  margin-left: auto !important;

  a {
    margin-top: 0 !important;

    &:nth-child(2) {
      margin-left: 20px;
    }
  }
}

.modal-socials {
  display: flex;
  width: 185px;
  justify-content: space-between;

  a {
    width: 45px;
    height: 45px;

    svg {
      width: 18px;
      height: 18px;
    }
  }
}

.form-checkbox.modal-checkbox {
  margin-top: 0;
}

.modal-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 17px;
}

@media screen and (max-width: 1440px) {
  .modal__container {
    padding: 30px 40px;
  }

  .modal__close {
    top: -21px;
    right: -10px;
  }

  .modal__container h2.modal__title {
    font-size: 28px;
  }

  .modal__subtitle {
    margin-top: 20px;
  }

  .modal__container .form-row input {
    font-size: 16px;
    padding: 10px;
  }

  .choices__list {
    font-size: 16px;
  }

  .choices__inner {
    padding: 10px !important;
  }

  .modal__container .form-textarea {
    font-size: 16px;
    padding: 10px;
  }
}

@media screen and (max-width: 768px) {
  .modal__container .form-row:nth-child(2) {
    margin-top: 0 !important;
  }
  .modal__container {
    overflow-x: hidden;
    max-width: none;
    margin: 0 20px;
    padding: 35px 15px 27px;

    .form-row {
      flex-direction: column;
    }

    > .form-row.choices__list {
      font-size: 14px;
    }

    .choices__list--single .choices__item {
      font-size: 14px;
    }
  }

  .modal-btn.form-btn {
    text-align: left;
    width: 100%;
  }

  .modal__container .form-row .choices {
    width: 100%;
  }

  .modal__title {
    font-size: 23px;
    line-height: 125%;
  }

  .modal__close {
    opacity: 0;
  }

  .form-row .choices .choices__inner {
    font-size: 14px;
    line-height: 17px;
    padding: 12px 10px;
  }

  .modal__subtitle {
    font-size: 20px;
    line-height: 27px;
    text-align: center;
    margin-top: 30px;
  }

  .modal__container .form-row input {
    font-size: 14px;
    line-height: 17px;
    padding: 12px 10px;
  }

  .modal__container .form-textarea {
    margin-top: 80px;
    font-size: 14px;
    line-height: 17px;
    padding: 12px 10px;
    width: 100%;
  }

  .modal__btn_wrap {
    flex-direction: column;
  }

  .modal-contacts {
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
    margin-left: 0 !important;
    width: 100%;
  }

  .modal-contacts a:nth-child(2) {
    margin-left: 0;
    margin-top: 10px !important;
  }

  .modal-bottom {
    flex-direction: column;
  }

  .modal-socials {
    margin-top: 16px;
  }
}
