.interview {
  padding-bottom: 153px;
}

.interview-title {
  font-family: 'Cormorant', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 80px;
  line-height: 143.5%;
  letter-spacing: -0.02em;
  color: #5E534A;
  text-align: center;
}

.interview__video {
  width: 935px;
  height: 527px;
  margin: 37px auto 0;

  iframe {
    width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 1200px) {
  .interview-title {
    font-size: 60px;
  }
}

@media screen and (max-width: 1024px) {
  .interview-title {
    font-size: 48px;
  }
  .interview__video {
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .interview-title {
    font-size: 32px;
  }
}

@media screen and (max-width: 425px) {
  .interview {
    padding-top: 22px;
    padding-bottom: 76px;
  }

  .interview__video {
    margin: 20px auto 0;
    height: 159px;
  }
}